import { BigNumber } from "@ethersproject/bignumber";
import { formatEther } from "@ethersproject/units";

import { ITokenData } from "../types/ITokenData";

const setTokenDataKV = (
  // o: { [key: string]: string },
  o: ITokenData,
  k: keyof ITokenData,
  v: string
) => {
  let n = Object.assign({}, o);
  n[k] = v;
  return n;
};

const formatEth = (num: BigNumber | undefined, decimals: number) => {
  if (num) return parseFloat(formatEther(num)).toFixed(decimals);
  else return "?".repeat(decimals);
};

const isKeyValueEmptyOrNull = (
  td: ITokenData,
  k: keyof ITokenData
): boolean => {
  if (td[k] && td[k] !== "") return false;
  return true;
};

const hasTokenData = (td: ITokenData, keys: string[]): boolean => {
  for (let i in keys) {
    let k = keys[i] as keyof ITokenData;
    if (isKeyValueEmptyOrNull(td, k)) return false;
  }
  return true;
};

const tokenDataIsValid = (td: ITokenData, keys: string[]): boolean => {
  for (let i in keys) {
    let k = keys[i] as keyof ITokenData;
    let v: string = td[k];

    for (let i = 0; i < v.length; i++) {
      let c = v.charCodeAt(i);
      if (c < 32 || c === 38 || c === 60 || c === 62 || c > 126) return false;
    }
  }

  return true;
};

const strEqualsIgnoreCase = (a: string | undefined, b: string | null) => {
  if (a && b) return a.toLowerCase() === b.toLowerCase();
  else return false;
};

export {
  setTokenDataKV,
  isKeyValueEmptyOrNull,
  formatEth,
  hasTokenData,
  tokenDataIsValid,
  strEqualsIgnoreCase,
};
