import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import { useContractCall } from "@usedapp/core";

import { CollectionGrid } from "../components/CollectionGrid";
import { IBannerNFTs } from "../contracts/BannerNFTs";
import { ADDRESS_BANNER_NFT_CONTRACT } from "../types/consts";

function Collection() {
  const { t } = useTranslation();

  const numMintedNFTs = useContractCall({
    abi: IBannerNFTs,
    address: ADDRESS_BANNER_NFT_CONTRACT,
    method: "totalSupply",
    args: [],
  }) as unknown;

  return (
    <div id="collection">
      {/* title */}
      <h1 className="page-title">{t("collection:title")}</h1>

      {/* subtitle */}
      <div className="page-subtitle">{t("collection:subtitle")}</div>

      {/* link to mint */}
      <Link
        key={-1}
        to="/mint"
        className="block my-2 md:my-5 relative py-14 mint-this-nft"
      >
        <div className="text-center md:absolute md:top-2/4 md:left-2/4 md:-translate-x-1/2 md:-translate-y-1/2">
          <p>{t("collection:unminted")}</p>
          {numMintedNFTs && (numMintedNFTs as number) > 0 ? (
            <p className="mt-3 text-xs text-center">
              {t("collection:numMinted", { numMinted: numMintedNFTs })}
            </p>
          ) : (
            <p></p>
          )}
        </div>
      </Link>

      {/* grid */}
      <CollectionGrid />
    </div>
  );
}

export default Collection;
