import { BigNumber } from "@ethersproject/bignumber";
import { Provider } from "@ethersproject/providers";
import { ChainId, useBlockNumber, useEthers } from "@usedapp/core";
import { ethers } from "ethers";
import { useEffect, useState } from "react";

import { ITokenData } from "../types/ITokenData";
import { setTokenDataKV } from "../utils/Misc";
import { bannerNFTsContract } from "../contracts/BannerNFTs";
import AwesomeDebouncePromise from "awesome-debounce-promise";

const DEFAULT_PREVIEW_DATA = {
  image:
    "data:image/svg+xml;base64,",
};

function getProvider(chainId: ChainId | undefined) {
  if (!chainId) chainId = 1;

  let provider = ethers.providers.getDefaultProvider(chainId, {
    alchemy: "qFOIOqkr3RT2YOile-hYtPeYEownG7kf",
  });

  return provider;
}

async function previewNFT(provider: Provider | undefined, tokenData: ITokenData, setResult: Function) {
  if (!provider) {
    console.log("not connected");
    return;
  }

  if (!("text" in tokenData) || tokenData.text?.length === 0) {
    setResult(DEFAULT_PREVIEW_DATA);

    return;
  }

  let contract = bannerNFTsContract.connect(provider);
  let b64data = await contract.previewTokenURI(
    tokenData["name"] || "",
    tokenData["description"] || "",
    tokenData["bgColor"] || "",
    tokenData["text"],
    tokenData["textColor"] || "",
    tokenData["textSize"] || ""
  );
  if (b64data) {
    let previewData = JSON.parse(window.atob(b64data.substring(29)));
    let newTokenData = setTokenDataKV(tokenData, "image", previewData["image"]);
    setResult(newTokenData);
  } else {
    console.log("something's gone wrong... b64data is empty");
    throw new Error("empty response from contract.previewTokenURI");
  }
}

const previewNFTDebounced = AwesomeDebouncePromise(previewNFT, 500);

async function fetchTokenById(chainId: ChainId | undefined, setResult: Function, tokenId: string | undefined) {
  let provider = getProvider(chainId);

  let contract = bannerNFTsContract.connect(provider);

  let dataURI = (await contract.tokenURI(tokenId)).toString();
  let data = JSON.parse(window.atob(dataURI.substring(29)));
  data["raw"] = dataURI;

  let owner = (await contract.ownerOf(tokenId)).toString();
  data["owner"] = owner;

  let token = await contract.getToken(tokenId);
  data["tokenId"] = tokenId;
  data["name"] = token[0];
  data["description"] = token[1];
  data["bgColor"] = token[2];
  data["text"] = token[3];
  data["textColor"] = token[4];
  data["textSize"] = token[5];

  setResult(data);
}

async function fetchCollectionTokens(chainId: ChainId | undefined, numTokensToFetch: number, setResult: Function) {
  let nfts: Array<any> = [];

  let provider = getProvider(chainId);

  let contract = bannerNFTsContract.connect(provider);
  let numTokens: number = await contract.totalSupply();
  numTokens = Math.min(numTokens, numTokensToFetch);

  for (let i = 0; i < numTokens; i++) {
    let tokenId = await contract.tokenByIndex(i);
    let dataURI = (await contract.tokenURI(tokenId)).toString();
    let data = JSON.parse(window.atob(dataURI.substring(29)));
    data.owner = (await contract.ownerOf(tokenId)).toString();
    data.tokenId = tokenId.toNumber();
    nfts[i] = data;
  }

  setResult(nfts);
}

async function fetchTokensByOwnerAddress(
  provider: Provider | undefined,
  address: string | undefined | null,
  setResult: Function
) {
  if (provider && address) {
    let nfts: Array<any> = [];
    let contract = bannerNFTsContract.connect(provider);
    let numTokens: number = await contract.balanceOf(address);

    for (let i = 0; i < numTokens; i++) {
      let tokenId = await contract.tokenOfOwnerByIndex(address, i);
      let dataURI = (await contract.tokenURI(tokenId)).toString();
      let data = JSON.parse(window.atob(dataURI.substring(29)));
      data.tokenId = tokenId.toNumber();
      nfts[i] = data;
    }

    setResult(nfts);
  }
}

async function getMintingCost(provider: Provider | undefined) {
  if (!provider) provider = ethers.providers.getDefaultProvider();

  let contract = bannerNFTsContract.connect(provider);
  return contract.getMintingCost();
}

function useMintingCost(): BigNumber | undefined {
  const { library } = useEthers();
  const blockNumber = useBlockNumber();
  const [mintingCost, setMintingCost] = useState<BigNumber | undefined>();

  useEffect(() => {
    async function updateMintingCost() {
      setMintingCost(await getMintingCost(library));
    }

    updateMintingCost();
  }, [library, blockNumber]);

  return mintingCost;
}

export {
  DEFAULT_PREVIEW_DATA,
  fetchCollectionTokens as fetchCollectionNFTs,
  fetchTokensByOwnerAddress as fetchTokensOfAddress,
  fetchTokenById as fetchNFT,
  previewNFT,
  useMintingCost,
  getMintingCost,
  previewNFTDebounced,
};
