import { useTranslation } from "react-i18next";
import { useContext, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useContractCall, useEthers } from "@usedapp/core";

import { AppContext } from "../contexts/app-context";
import { IBannerNFTs } from "../contracts/BannerNFTs";
import { fetchTokensOfAddress } from "../contracts/helpers";
import { TransactionsList } from "../components/TransactionsList";
import { ADDRESS_BANNER_NFT_CONTRACT } from "../types/consts";

import "../styles/collection.css";
import "../styles/common.css";

function MyNFTs() {
  const { t } = useTranslation();
  const { account } = useContext(AppContext);
  const { library } = useEthers();

  const [myNFTs, setMyNFTs] = useState<Array<any> | null>(null);

  const numOwnedNFTs = useContractCall({
    abi: IBannerNFTs,
    address: ADDRESS_BANNER_NFT_CONTRACT,
    method: "balanceOf",
    args: [account],
  })?.toString();

  useEffect(() => {
    fetchTokensOfAddress(library, account, setMyNFTs);

    return function cleanup() {
      setMyNFTs([]);
    };
  }, [account, library]);

  function _renderNFT(idx: number, nft: any) {
    return (
      <Link className="nft" key={idx} to={"/collection/" + nft["tokenId"]}>
        <img src={nft["image"].toString()} alt={nft["name"]} />
        <div className="overlay">
          <p className="text-sm font-medium">
            {t("collection:token", { tokenId: nft["tokenId"] })}&nbsp;
            {nft["name"]}
          </p>
        </div>
      </Link>
    );
  }

  function _renderNFTs() {
    let nfts: any[] = [];

    if (myNFTs != null) {
      const n = myNFTs.length;

      for (let i = 0; i < n; i++) {
        nfts.push(_renderNFT(i, myNFTs[i]));
      }

      return nfts;
    } else {
      return (
        <div className="min-h-screen col-span-2 mx-10 mt-20 text-center text-2xl">
          {t("myNfts:loading")}
        </div>
      );
    }
  }

  return (
    <div id="my-nfts">
      {/* TODO: externalize strings */}
      <h1 className="page-title">🛍️ My NFTs 🛍️</h1>
      <p className="page-subtitle">
        In the Metaverse, everyone knows what you have 🕵️.
      </p>

      {!numOwnedNFTs ? (
        // numOwnedNFTs == undefined
        <div className="min-h-screen md:col-span-2 mx-10 mt-20 text-center text-2xl">
          {t("myNfts:loading")}
        </div>
      ) : parseInt(numOwnedNFTs) > 0 ? (
        // numOwnedNFTs > 0
        // TODO: pluralize
        <div className="mt-5">
          <p className="text-lg">
            Whoa, you own {numOwnedNFTs} <s>Bags</s> Banner NFTs 🐋. Noice 👌!
          </p>
          <div className="md:grid md:grid-cols-2 md:grid-flow-row">
            {_renderNFTs()}
          </div>
        </div>
      ) : (
        // numOwnedNFTs == 0
        <p className="mt-5">huh.. go mint something 🦍</p>
      )}

      {!numOwnedNFTs ? (
        <></>
      ) : (
        <div className="hidden md:block md:mt-32">
          <TransactionsList />
        </div>
      )}
    </div>
  );
}

export default MyNFTs;
