import { useEthers } from "@usedapp/core";
import { isAddress } from "@ethersproject/address";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useLocation } from "react-router-dom";

import { useMintingCost } from "../contracts/helpers";
import { ITokenData } from "../types/ITokenData";
import {
  formatEth,
  hasTokenData,
  setTokenDataKV,
  tokenDataIsValid,
} from "../utils/Misc";
import { MAX_TEXT_LENGTH } from "../types/consts";

import "../styles/common.css";

enum MinterFormStatus {
  ENABLED,
  DISABLED,
  INCOMPLETE,
  INVALID,
}

export function Minter({
  onTokenDataChange,
  callPayAndMint,
  setProcessing,
}: {
  onTokenDataChange: Function;
  callPayAndMint: Function;
  setProcessing: Function;
}) {
  const { t } = useTranslation();

  const oldTokenData: any = useLocation().state;
  const [tokenData, setTokenData] = useState<ITokenData>(
    oldTokenData || {
      randomizeColors: true,
    }
  );
  const [formStatus, setFormStatus] = useState<MinterFormStatus>(
    MinterFormStatus.DISABLED
  );
  const [showAddress, setShowAddress] = useState(false);

  let mintingCost = useMintingCost();

  const onNameChangeHandler = (evt: any) => {
    let td = setTokenDataKV(tokenData, "name", evt.target.value);
    setTokenData(td);
    onTokenDataChange(td);
  };

  const onDescriptionChangeHandler = (evt: any) => {
    let td = setTokenDataKV(tokenData, "description", evt.target.value);
    setTokenData(td);
    onTokenDataChange(td);
  };

  const onTextChangeHandler = (evt: any) => {
    let td = setTokenDataKV(tokenData, "text", evt.target.value);
    setTokenData(td);
    onTokenDataChange(td);
  };

  const onRandomizeColorsClickHandler = (evt: any) => {
    let td = setTokenDataKV(tokenData, "randomizeColors", evt.target.checked);
    setTokenData(td);
    onTokenDataChange(td);
  };

  const onShowAddressClickHandler = (evt: any) => {
    setShowAddress(evt.target.checked);
  };

  const onAddressChangeHandler = (evt: any) => {
    let td = setTokenDataKV(tokenData, "address", evt.target.value);
    setTokenData(td);
    onTokenDataChange(td);
  };

  const { account } = useEthers();
  const payAndMint = () => {
    if (!mintingCost) return;

    // disable the button while processing
    setFormStatus(MinterFormStatus.DISABLED);

    // create tx that calls the contract to pay and mint the NFT
    callPayAndMint(
      tokenData["name"],
      tokenData["description"],
      tokenData["text"],
      tokenData["randomizeColors"],
      tokenData["address"] || account,
      {
        value: mintingCost,
        gasLimit: 1000000,
      }
    );

    // re-enable the button once finished
    setFormStatus(MinterFormStatus.ENABLED);

    // show processing view
    setProcessing(true);
  };

  const isTokenDataFilled = (
    td: ITokenData,
    keys: string[],
    showAddress: boolean
  ): boolean => {
    if (hasTokenData(td, keys)) {
      if (showAddress && !isAddress(td["address"]!)) return false;
      else return true;
    }

    return false;
  };

  // hook that handles updates to the pay components
  useEffect(() => {
    // text is too long
    if (tokenData["text"] && tokenData["text"].length > MAX_TEXT_LENGTH) {
      setFormStatus(MinterFormStatus.INVALID);
      return;
    }

    let _formStatus;
    const keys = ["name", "description", "text"];
    // only enable if tokenData has all fields filled (name, description, text)
    if (isTokenDataFilled(tokenData, keys, showAddress)) {
      if (!tokenDataIsValid(tokenData, keys))
        _formStatus = MinterFormStatus.INVALID;
      else _formStatus = MinterFormStatus.ENABLED;
    } else {
      _formStatus = MinterFormStatus.INCOMPLETE;
    }

    setFormStatus(_formStatus);
  }, [tokenData, showAddress]);

  return (
    <div>
      {/* token name input */}
      <label className="block mt-5 mb-5">
        <p>
          <span>{t("mint:labelName")}</span>
          <span className="ml-1 text-xs italic">{t("mint:warningName")}</span>
        </p>
        <input
          type="text"
          name="tokenName"
          onChange={onNameChangeHandler}
          className="text-input-fw"
          placeholder={t("mint:placeholderName")}
          defaultValue={tokenData["name"] ? tokenData["name"] : ""}
        />
      </label>

      {/* token description input */}
      <label className="block mt-5 mb-5">
        <p>
          <span>{t("mint:labelDescription")}</span>
          <span className="ml-1 text-xs italic">
            {t("mint:warningDescription")}
          </span>
        </p>
        <input
          type="text"
          name="tokenDescription"
          onChange={onDescriptionChangeHandler}
          className="text-input-fw"
          placeholder={t("mint:placeholderDescription")}
          defaultValue={
            tokenData["description"] ? tokenData["description"] : ""
          }
        />
      </label>

      {/* token text input */}
      <label className="block mt-5 mb-5">
        <p>
          <span>{t("mint:labelText")}</span>
          <span className="ml-1 text-xs italic">{t("mint:warningText")}</span>
        </p>
        <input
          type="text"
          name="tokenText"
          onChange={onTextChangeHandler}
          className="text-input-fw"
          placeholder={t("mint:placeholderText")}
          defaultValue={tokenData["text"] ? tokenData["text"] : ""}
        />
        <p className="text-xs italic mt-1 text-right">
          {MAX_TEXT_LENGTH - (tokenData["text"] ? tokenData["text"].length : 0)}{" "}
          characters left for your text.
        </p>
      </label>

      {/* randomize checkbox */}
      <label className="block mt-5 mb-5">
        <div className="inline-flex items-center">
          <input
            type="checkbox"
            className="focus:ring-black border-2"
            onClick={onRandomizeColorsClickHandler}
            defaultChecked={tokenData["randomizeColors"] ? true : false}
          />
          <span className="ml-3">
            {t(
              tokenData["randomizeColors"]
                ? "mint:labelRandomize"
                : "mint:labelDoNotRandomize"
            )}
          </span>
        </div>
      </label>

      {/* token address checkbox */}
      <label className="block">
        <div className="inline-flex items-center">
          <input
            type="checkbox"
            className=" focus:ring-black border-2"
            onClick={onShowAddressClickHandler}
            defaultChecked={tokenData["address"] ? true : false}
          />
          <span className="ml-3">{t("mint:labelCheck")}</span>
        </div>
      </label>

      {/* token address input */}
      <label className={showAddress ? "visible" : "hidden"}>
        <p className="mt-3">
          <span>{t("mint:labelTargetAddress")}</span>
          <span className="ml-1 text-xs italic">
            {t("mint:warningTargetAddress")}
          </span>
        </p>
        <input
          type="text"
          name="address"
          className="text-input-fw"
          onChange={onAddressChangeHandler}
          placeholder={t("mint:placeholderTargetAddress")}
          defaultValue={tokenData["address"] ? tokenData["address"] : ""}
        />
      </label>

      {/* pay and mint button */}
      <label className="block pt-10 pb-3">
        <button
          onClick={payAndMint}
          className={
            formStatus === MinterFormStatus.ENABLED
              ? "btn-enabled"
              : "btn-disabled"
          }
          disabled={formStatus !== MinterFormStatus.ENABLED}
        >
          {t("mint:payAndMint")}
        </button>

        <span className="ml-2 text-xs">
          {t("mint:cost", {
            cost: formatEth(useMintingCost(), 4),
          })}
        </span>
      </label>

      {/* error msg */}
      {formStatus !== MinterFormStatus.ENABLED && (
        <div className="text-sm p-4 rounded bg-yellow-500">
          <span>🚨</span>
          <span className="italic mx-1 text-black">
            {formStatus === MinterFormStatus.INCOMPLETE &&
              t("mint:incompleteForm")}
            {formStatus === MinterFormStatus.INVALID && t("mint:invalidForm")}
          </span>
          <span>🚨</span>
        </div>
      )}
    </div>
  );
}
