import { TransactionReceipt } from "@ethersproject/providers";
import { StoredTransaction, useTransactions } from "@usedapp/core";
import { BigNumber } from "ethers";
import { formatEth } from "../utils/Misc";

export function TransactionsList() {
  const { transactions } = useTransactions();

  const _txIsSuccessful = (txReceipt: TransactionReceipt | undefined) => {
    if (txReceipt) {
      if (txReceipt.status === 1) return true;
    }

    return false;
  };

  const _calcTxCost = (tx: StoredTransaction, decimals: number) => {
    if (tx.receipt) {
      let x = BigNumber.from(tx.receipt.cumulativeGasUsed).mul(
        BigNumber.from(tx.receipt.effectiveGasPrice)
      );
      return formatEth(BigNumber.from(tx.transaction.value).add(x), decimals);
    } else {
      return "???";
    }
  };

  const _renderTxs = (transactions: StoredTransaction[]) => {
    let renderedTxs: JSX.Element[] = [];
    const dtOptions: Intl.DateTimeFormatOptions = {
      hour12: false,
      day: "2-digit",
      month: "long",
      year: "numeric",
      hour: "2-digit",
      minute: "2-digit",
      second: "2-digit",
    };
    let dtFormatter = Intl.DateTimeFormat("en", dtOptions);

    for (let i = 0; i < Math.min(transactions.length, 20); i++) {
      let tx = transactions[i];
      renderedTxs.push(
        <div
          key={i}
          className="grid grid-cols-5 grid-flow-row py-1 text-base auto-cols-min auto-rows-min"
        >
          <span>{dtFormatter.format(new Date(tx.submittedAt))}</span>
          <a
            href={"https://etherscan.io/tx/" + tx.transaction.hash}
            className="col-span-2 link-underline"
            target="_blank"
            rel="noreferrer"
          >
            <span>{tx.transactionName}</span>
          </a>

          <span className="text-right">{_calcTxCost(tx, 8)}Ξ</span>

          <span className="text-right">
            {_txIsSuccessful(tx.receipt) ? "Successful" : "Failed!"}
          </span>
        </div>
      );
    }

    return renderedTxs;
  };

  // TODO: externalize and pluralize strings
  return (
    <>
      <p className="text-2xl mb-1">Look at the mess you've made! 👀</p>
      <p className="text-sm mb-4 italic">
        Showing the last 20 transactions. 📜
      </p>

      {transactions ? (
        transactions.length > 0 ? (
          <div className="pb-4">{_renderTxs(transactions)}</div>
        ) : (
          <div className="py-48 text-xl">
            Oh nevermind, it's disappointingly clean 🧹!
          </div>
        )
      ) : (
        <div className="py-48 text-xl">aNaLyZinG tEh BLoCkcHaiNzZzZZz</div>
      )}
    </>
  );
}
