import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { Link, useLocation } from "react-router-dom";
import { useContractCall, useContractFunction, useEthers } from "@usedapp/core";

import { ProcessingTx } from "../components/ProcessingTx";
import { bannerNFTsContract, IBannerNFTs } from "../contracts/BannerNFTs";
import {
  DEFAULT_PREVIEW_DATA,
  previewNFTDebounced,
} from "../contracts/helpers";
import { IPreviewData } from "../types/IPreviewData";
import { ITokenData } from "../types/ITokenData";
import { hasTokenData, tokenDataIsValid } from "../utils/Misc";
import {
  ADDRESS_BANNER_NFT_CONTRACT,
  MAX_TEXT_LENGTH,
  UPDATE_TOKEN_COST,
  UPDATE_TOKEN_COST_VALUE,
} from "../types/consts";

import "../styles/common.css";

// TODO: externalize strings
function EditNFT() {
  const { t } = useTranslation();
  const { library } = useEthers();
  const nftData: ITokenData = useLocation().state as ITokenData;
  const [changedNFTData, setChangedNFTData] = useState<any | undefined>(
    nftData
  );
  const [changedNFTImage, setChangedNFTImage] = useState<IPreviewData>({
    image:
      nftData && "image" in nftData
        ? nftData["image"]
        : DEFAULT_PREVIEW_DATA.image,
  });
  const [canModifyTextSize, setCanModifyTextSize] = useState<boolean>(false);
  const [canModifyTextColor, setCanModifyTextColor] = useState<boolean>(false);
  const [canModifyBgColor, setCanModifyBgColor] = useState<boolean>(false);
  const [hasNoMaxTextLength, setHasNoMaxTextLength] = useState<boolean>(false);

  const [formValid, setIsFormValid] = useState<boolean>(false);
  const [isProcessing, setProcessing] = useState<boolean>(false);

  const { state: updateAttributesState, send: updateAttributes } =
    useContractFunction(bannerNFTsContract, "updateSvgAttributes", {
      transactionName: "Updating NFT Attributes",
    });

  const modifiableAttrs = useContractCall({
    abi: IBannerNFTs,
    address: ADDRESS_BANNER_NFT_CONTRACT,
    method: "getModifiableAttrs",
    args: [nftData["tokenId"]],
  });
  useEffect(() => {
    if (modifiableAttrs) {
      setCanModifyTextColor(modifiableAttrs[0][0]);
      setCanModifyTextSize(modifiableAttrs[0][1]);
      setCanModifyBgColor(modifiableAttrs[0][2]);
      setHasNoMaxTextLength(modifiableAttrs[0][3]);
    }
  }, [modifiableAttrs]);

  useEffect(() => {
    if (changedNFTData) {
      const tokenDataKeys = ["text", "textSize", "textColor", "bgColor"];

      setIsFormValid(
        hasTokenData(changedNFTData, tokenDataKeys) &&
          tokenDataIsValid(changedNFTData, tokenDataKeys)
      );
    }
  }, [changedNFTData]);

  function updateChangedNFTData(k: string, v: string) {
    // update internal state
    let newChangedNFTData = Object.assign({}, changedNFTData);
    newChangedNFTData[k] = v;
    setChangedNFTData(newChangedNFTData);

    // trigger a call to preview
    previewNFTDebounced(library, newChangedNFTData, setChangedNFTImage);
  }

  const onSaveHandler = () => {
    // create a tx and execute in smart contract
    updateAttributes(
      changedNFTData["tokenId"],
      changedNFTData["text"],
      changedNFTData["textColor"],
      changedNFTData["textSize"],
      changedNFTData["bgColor"],
      {
        value: UPDATE_TOKEN_COST,
      }
    );

    // show processing view
    setProcessing(true);
  };

  return isProcessing ? (
    <ProcessingTx
      txStatus={updateAttributesState}
      tokenData={changedNFTImage}
    />
  ) : (
    <div id="browse">
      <div className="flex flex-col">
        {changedNFTData ? (
          <div>
            {/* token name as page title */}
            <h1 className="page-title long-text">
              ✍️ {t("collection:token", { tokenId: changedNFTData["tokenId"] })}
              &nbsp;—&nbsp;{changedNFTData["name"]}
            </h1>
            {/* token description as page subtitle */}
            <p className="page-subtitle italic">
              {changedNFTData["description"]}
            </p>

            {/* the image */}
            <img
              className="letterboard mt-1 mb-1"
              width="100%"
              height="100%"
              src={changedNFTImage["image"]}
              alt={changedNFTData["name"]}
            />

            {/* the text */}
            <blockquote className="mt-2 p-5">
              <div className="h-3 text-5xl text-left">“</div>

              <p className="my-4">
                <input
                  type="text"
                  onChange={(evt: any) => {
                    let s: string = evt.target.value;
                    if (s.length > MAX_TEXT_LENGTH)
                      s = s.substring(0, MAX_TEXT_LENGTH);
                    updateChangedNFTData("text", s);
                  }}
                  className="text-input-fw text-md long-text text-center"
                  value={changedNFTData["text"]}
                />
              </p>
              {!hasNoMaxTextLength && (
                <p className="text-xs text-left italic">
                  {MAX_TEXT_LENGTH - changedNFTData["text"].length} characters
                  left for your text.
                </p>
              )}

              <div className="h-3 text-5xl text-right">”</div>
            </blockquote>

            {/* other attributes */}
            <p className="my-2">
              <span className="block md:inline mb-4 md:mb-0">
                <span className="block md:inline font-semibold md:mr-1">
                  Text Size:
                </span>
                <input
                  type="text"
                  onChange={(evt: any) => {
                    updateChangedNFTData("textSize", evt.target.value);
                  }}
                  className="text-input"
                  disabled={canModifyTextSize ? false : true}
                  value={changedNFTData["textSize"]}
                />
              </span>

              <span className="block md:inline mb-4 md:mb-0 md:ml-3">
                <span className="block md:inline font-semibold md:mr-1">
                  Text Color:
                </span>
                <input
                  type="text"
                  onChange={(evt: any) => {
                    updateChangedNFTData("textColor", evt.target.value);
                  }}
                  className="text-input"
                  disabled={canModifyTextColor ? false : true}
                  value={changedNFTData["textColor"]}
                />
              </span>

              <span className="block md:inline mb-4 md:mb-0 md:ml-3">
                <span className="block md:inline font-semibold md:mr-1">
                  Background Color:
                </span>
                <input
                  type="text"
                  onChange={(evt: any) => {
                    updateChangedNFTData("bgColor", evt.target.value);
                  }}
                  className="text-input"
                  disabled={canModifyBgColor ? false : true}
                  value={changedNFTData["bgColor"]}
                />
              </span>
            </p>

            {/* save/cancel changes */}
            <div className="mt-10 text-md">
              <div>
                <button
                  className={formValid ? "btn-enabled" : "btn-disabled"}
                  onClick={onSaveHandler}
                  disabled={!formValid}
                >
                  💾 Save changes
                </button>

                <Link
                  to={"/collection/" + nftData["tokenId"]}
                  className="btn-cancel inline-block mt-2 md:mt-0 md:ml-5 md:inline"
                >
                  ❌ Cancel changes
                </Link>

                {!formValid && (
                  <span className="mx-5 p-2 rounded bg-yellow-200 text-black font-bold">
                    🚨 Fill all the fields, and don't use invalid characters
                    (e.g. &lt;&gt;&amp;) 🚨
                  </span>
                )}
              </div>
              <p className="mt-3 text-xs">
                Note: Saving the changes will cost {UPDATE_TOKEN_COST_VALUE}Ξ +
                gas (variable).
              </p>
            </div>
          </div>
        ) : (
          <div className="text-center">
            <p className="mt-28 mb-8 text-5xl">{t("collection:fishy")}</p>
            <p className="my-4 text-3xl">{t("collection:rightPlace")}</p>
            <p className="text-3xl">{t("collection:bug")}</p>
            <p className="mt-8 text-base italic">
              <a
                className="link-underline"
                href={"https://twitter.com/banner_nfts"}
                target="_blank"
                rel="noreferrer"
              >
                {t("collection:contact", { contact: "banner_nfts" })}
              </a>
            </p>
          </div>
        )}
      </div>
    </div>
  );
}

export default EditNFT;
