import { TransactionStatus } from "@usedapp/core";
import { Link } from "react-router-dom";
import { ITokenData } from "../types/ITokenData";

// TODO: strings

const _processing = (txhash: string | undefined, status: string) => {
  if (status === "PendingSignature" || status === "Mining")
    return (
      <div className="m-auto">
        <div>Your transaction has been sent to the nOoOooOOoodEzzz... 🔄</div>

        <div className="text-base mt-10">
          Wait here for a bit. I mean, you can browse around the website if you
          want. Or go 🚽 or ☕ or whatever.
        </div>

        <div className="text-sm mt-10">
          ➡️ Or put <span className="text-xs">{txhash}</span> into a block
          explorer, e.g.
          <a
            className="ml-1 link-underline"
            href={"https://etherscan.io/tx/" + txhash}
            target="_blank"
            rel="noreferrer"
          >
            Etherscan
          </a>
          , if you like to do that kind of stuff.
        </div>
      </div>
    );
};

const _succeeded = (
  txhash: string | undefined,
  status: string,
  tokenData: ITokenData
) => {
  if (status === "Success")
    return (
      <div className="m-auto">
        <div className="text-2xl">
          Your transaction has been successfully completed! 🎉
        </div>

        <div className="mt-4">
          {tokenData["tokenId"] ? (
            <Link
              to={"/collection/" + tokenData["tokenId"]}
              className="text-base"
            >
              ➡️ Check out your changes!
            </Link>
          ) : (
            <Link to="/my-nfts" className="text-base">
              ➡️ Go check your NFTs
            </Link>
          )}
        </div>

        <div className="text-sm mt-10">
          ➡️ Or put <span className="text-xs">{txhash}</span> into a block
          explorer, e.g.
          <a
            className="ml-1 link-underline"
            href={"https://etherscan.io/tx/" + txhash}
            target="_blank"
            rel="noreferrer"
          >
            Etherscan
          </a>
          , if you like to do that kind of stuff.
        </div>
      </div>
    );
};

const _failed = (
  txhash: string | undefined,
  status: string,
  errorMsg: string | undefined,
  tokenData: any
) => {
  if (status === "Fail" || status === "Exception")
    return (
      // TODO: what happens if error/validations fail?
      <div className="m-auto">
        <div>Huh oh, something's gone wrong... 🤖</div>

        <div className="text-lg mt-4 mb-8">
          ⚠️
          <span className="mx-2 italic inline-block text-red-400">
            {errorMsg}
          </span>
          ⚠️
        </div>

        {txhash ? (
          <div className="text-sm mt-10">
            <div>
              ➡️ Find out more in a block explorer, e.g.
              <a
                className="ml-1 link-underline"
                href={"https://etherscan.io/tx/" + txhash}
                target="_blank"
                rel="noreferrer"
              >
                Etherscan
              </a>
            </div>
            <div>
              The transaction hash is <span className="text-xs">{txhash}</span>.
            </div>
          </div>
        ) : (
          <></>
        )}

        <Link
          to=""
          state={tokenData}
          className="text-base mt-8 inline-block"
          key={Date.now()}
          onClick={() => window.location.reload()}
        >
          ➡️ Get back, get back, get back to where you once belonged
        </Link>

        <div className="text-xs mt-28">
          If you need some support, reach out to @banner_nfts with details
        </div>
      </div>
    );
};

export function ProcessingTx({
  txStatus,
  tokenData,
}: {
  txStatus: TransactionStatus;
  tokenData: ITokenData;
}) {
  return (
    <div className="flex h-screen text-2xl bold">
      {txStatus.status === "None" ? (
        <div className="m-auto">
          Waiting for you to confirm or reject the transaction...
        </div>
      ) : (
        <>
          {_processing(txStatus.transaction?.hash, txStatus.status)}
          {_succeeded(txStatus.transaction?.hash, txStatus.status, tokenData)}
          {_failed(
            txStatus.transaction?.hash,
            txStatus.status,
            txStatus.errorMessage,
            tokenData
          )}
        </>
      )}
    </div>
  );
}
