// TODO: add "view raw metadata"
import { useNetwork } from "@usedapp/core";
import { useContext, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { Link, useParams } from "react-router-dom";

import { AppContext } from "../contexts/app-context";
import { fetchNFT } from "../contracts/helpers";
import { ADDRESS_BANNER_NFT_CONTRACT } from "../types/consts";
import { ITokenData } from "../types/ITokenData";
import { strEqualsIgnoreCase } from "../utils/Misc";

import "../styles/view.css";
import "../styles/common.css";

function ViewNFT() {
  const { t } = useTranslation();
  const { network } = useNetwork();
  const { tokenId } = useParams();
  const { account } = useContext(AppContext);
  const [nftData, setNFTData] = useState<ITokenData | undefined>();
  const [showRawData, setShowRawData] = useState<boolean>(false);

  useEffect(() => {
    fetchNFT(network?.chainId, setNFTData, tokenId);
  }, [tokenId, network.chainId]);

  const onViewRawTokenURI = () => {
    setShowRawData(true);
  };

  return (
    <div id="browse">
      <div className="flex flex-col">
        {nftData ? (
          // <div className="text-center">
          //   <p className="mt-28 mb-8 text-5xl">{t("collection:fishy")}</p>
          //   <p className="my-4 text-3xl">{t("collection:rightPlace")}</p>
          //   <p className="text-3xl">{t("collection:bug")}</p>
          //   <p className="mt-8 text-base italic">
          //     <a
          //       className="link-underline"
          //       href={"https://twitter.com/banner_nfts"}
          //       target="_blank"
          //       rel="noreferrer"
          //     >
          //       {t("collection:contact", { contact: "banner_nfts" })}
          //     </a>
          //   </p>
          // </div>

          <div>
            {/* token name as page title */}
            <h1 className="page-title long-text">
              ✨ {t("collection:token", { tokenId: tokenId })}&nbsp;—&nbsp;
              {nftData["name"]}
            </h1>
            {/* token description as page subtitle */}
            <p className="page-subtitle italic">{nftData["description"]}</p>

            <p>
              {t("collection:owner", {
                owner: strEqualsIgnoreCase(nftData["owner"], account) ? t("collection:you") : nftData["owner"],
              })}
            </p>

            {/* the image */}
            <img
              className="letterboard mt-1 mb-1"
              width="100%"
              height="100%"
              src={nftData["image"]}
              alt={nftData["name"]}
            />

            {/* the text */}
            <blockquote className="mt-2 p-5">
              <div className="h-3 text-5xl text-left">“</div>

              <p className="my-4">
                <span className="text-md long-text block text-center">{nftData["text"]}</span>
              </p>

              <div className="h-3 text-5xl text-right">”</div>
            </blockquote>

            {/* other attributes */}
            <p className="my-2">
              <span className="block md:inline mb-4 md:mb-0">
                <span className="block md:inline font-semibold md:mr-1">Text Size:</span>
                <span className="italic">{nftData["textSize"]}</span>
              </span>

              <span className="hidden md:inline mx-3">➗</span>

              <span className="block md:inline mb-4 md:mb-0">
                <span className="block md:inline font-semibold md:mr-1">Text Color:</span>
                <span className="italic p-1.5 rounded" style={{ backgroundColor: nftData["textColor"] }}>
                  {nftData["textColor"]}
                </span>
              </span>

              <span className="hidden md:inline mx-3">➗</span>

              <span className="block md:inline mb-4 md:mb-0">
                <span className="block md:inline font-semibold md:mr-1">Background Color:</span>
                <span className="italic p-1.5 rounded" style={{ backgroundColor: nftData["bgColor"] }}>
                  {nftData["bgColor"]}
                </span>
              </span>
            </p>

            <div className="mt-10">
              {/* owner actions */}
              {strEqualsIgnoreCase(nftData["owner"], account) ? (
                <div>
                  <Link to="edit" state={nftData} className="btn-action text-md">
                    ✍️ Modify
                  </Link>
                </div>
              ) : (
                <></>
              )}

              <div className="mt-14 text-xs">
                {nftData && !showRawData ? (
                  <div className="cursor-pointer" onClick={onViewRawTokenURI}>
                    View Raw Token URI
                  </div>
                ) : (
                  <></>
                )}
                {showRawData ? <div className="long-text">{nftData["raw"]}</div> : <></>}
              </div>
            </div>

            <div className="inline-block text-xs mt-4 mb-1">
              {/* view externally */}
              <div>
                View in
                <a
                  className="mx-1 link-underline"
                  href={"https://looksrare.org/collections/" + ADDRESS_BANNER_NFT_CONTRACT + "/" + tokenId}
                  target="_blank"
                  rel="noreferrer"
                >
                  {t("collection:looksrare")}
                </a>
                |
                <a
                  className="mx-1 link-underline"
                  href={"https://opensea.io/assets/" + ADDRESS_BANNER_NFT_CONTRACT + "/" + tokenId}
                  target="_blank"
                  rel="noreferrer"
                >
                  {t("collection:opensea")}
                </a>
              </div>

              {/* share */}
              <div className="mt-4">
                <a
                  href={
                    "https://twitter.com/share?text=YO%21%20Check%20out%20my%20%23BannerNFT&url=" +
                    encodeURI(window.location.toString())
                  }
                  className="twitter-share-button link-underline"
                  data-text="YO! Check out my #BannerNFT"
                  data-dnt="true"
                  data-show-count="false"
                  target="_blank"
                  rel="noreferrer"
                >
                  Share on 🐦 twitter
                </a>
                <script async src="https://platform.twitter.com/widgets.js" charSet="utf-8"></script>
              </div>
            </div>
          </div>
        ) : (
          <div className="min-h-screen col-span-2 mx-10 mt-20 text-center text-2xl">{t("collection:loading")}</div>
        )}
      </div>
    </div>
  );
}

export default ViewNFT;
