import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useContractFunction, useEtherBalance, useEthers } from "@usedapp/core";

import { Minter } from "../components/Minter";
import {
  DEFAULT_PREVIEW_DATA,
  previewNFTDebounced,
} from "../contracts/helpers";
import { IPreviewData } from "../types/IPreviewData";
import { ITokenData } from "../types/ITokenData";
import { ProcessingTx } from "../components/ProcessingTx";
import { bannerNFTsContract } from "../contracts/BannerNFTs";
import { useLocation } from "react-router-dom";
import { formatEth } from "../utils/Misc";

function Mint() {
  const { t } = useTranslation();
  const { account, library } = useEthers();

  const ethBalance = useEtherBalance(account);

  const { state: callPayAndMintState, send: callPayAndMint } =
    useContractFunction(bannerNFTsContract, "payAndMint", {
      transactionName: "Generating NFT",
    });

  const [isProcessing, setProcessing] = useState<boolean>(false);

  const tokenData: any = useLocation().state;
  const [previewData, setPreviewData] = useState<IPreviewData>(
    tokenData ? tokenData : DEFAULT_PREVIEW_DATA
  );

  function onTokenDataChange(tokenData: ITokenData) {
    // don't call preview if text didn't change
    if (tokenData["text"] && tokenData["text"] === previewData["text"]) return;

    // trigger call to contract, and setPreviewData
    previewNFTDebounced(library, tokenData, setPreviewData);
  }

  return isProcessing ? (
    <ProcessingTx txStatus={callPayAndMintState} tokenData={previewData} />
  ) : (
    <div id="mint">
      <h1 className="page-title">{t("mint:title")}</h1>
      <p className="page-subtitle">{t("mint:subtitle")}</p>

      {ethBalance ? (
        <div className="mt-4 mb-8 text-sm">
          {t("mint:balance", {
            balance: formatEth(ethBalance, 4),
          })}
        </div>
      ) : (
        <></>
      )}

      {/* preview */}
      <div className="mt-2">
        <p className="text-md">{t("mint:preview")}</p>
        <img
          className="letterboard"
          width="100%"
          height="100%"
          src={previewData["image"]}
          alt={previewData["name"]}
        />
      </div>

      {/* minting form */}
      <Minter
        onTokenDataChange={onTokenDataChange}
        callPayAndMint={callPayAndMint}
        setProcessing={setProcessing}
      />
    </div>
  );
}

export default Mint;
